<template>
  <div class="tomato" :style="sizeStyle">
    <div class="leaf" :style="leafStyle"></div>
    <div v-if="showText" class="text">PODOMORO</div>
  </div>
</template>

<script>
export default {
  props: {
    size: {
      type: String,
      default: '25px'
    },
    showText: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {

    }
  },
  computed: {
    sizeStyle () {
      return {
        width: this.size,
        height: this.size
      }
    },
    leafStyle () {
      return {
        width: 'calc(' + this.size + ' * .2)',
        height: 'calc(' + this.size + ' * .27)',
        borderRadius: 'calc(' + this.size + ' * .1)'
      }
    }
  },
}
</script>

