.tomato {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #EA5548;
  border-radius: 50%;
  .leaf {
    position: absolute;
    right: 3.5%;
    top: 3.5%;
    background-color: #316901;
    transform: rotate(45deg);
  }
  .text {
    font-family: 'Lato, Light';
