<template>
  <div class="wrap">
    <div class="main-panel">
      <tomato :showText="true" size="250px"/>
    </div>
    <div class="nav">
      <button class="items" type="button" @click="openFuncPanel('add')">
        <img :src="addIcon" alt="">
      </button>
      <button class="items" type="button" @click="openFuncPanel('list')">
        <img :src="listIcon" alt="">
      </button>
      <button class="items" type="button" @click="openFuncPanel('analysis')">
        <img :src="analysisIcon" alt="">
      </button>
      <button class="items" type="button" @click="openFuncPanel('ringtone')">
        <img :src="ringtoneIcon" alt="">
      </button>
      <div class="toggle-panel" :class="{open: open}" @click="toggleFuncPanel">
        <tomato size="25px"/>
        <img class="arrow" src="../assets/icons/arrow.svg" alt="">
      </div>
    </div>
    <div v-show="open" class="func-panel">ccc</div>
  </div>
</template>

<script>
import tomato from './tomato.vue'
import add_white from '../assets/icons/add_white.svg'
import add_red from '../assets/icons/add_red.svg'
import list_white from '../assets/icons/list_white.svg'
import list_red from '../assets/icons/list_red.svg'
import analysis_white from '../assets/icons/analysis_white.svg'
import analysis_red from '../assets/icons/analysis_red.svg'
import ringtone_white from '../assets/icons/ringtone_white.svg'
import ringtone_red from '../assets/icons/ringtone_red.svg'
export default {
  components: {tomato},
  data () {
    return {
      open: false,
      activeFunc: 'add'
    }
  },
  computed: {
    addIcon () {
      return this.activeFunc === 'add' ? add_red : add_white
    },
    listIcon () {
      return this.activeFunc === 'list' ? list_red : list_white
    },
    analysisIcon () {
      return this.activeFunc === 'analysis' ? analysis_red : analysis_white
    },
    ringtoneIcon () {
      return this.activeFunc === 'ringtone' ? ringtone_red : ringtone_white
    },
  },
  methods: {
    openFuncPanel (func) {
      this.activeFunc = func
      this.open = true
    },
    closeFuncPanel () {
      this.open = false
    },
    toggleFuncPanel () {
      this.open = !this.open
    }
  }
}
</script>

<style lang="scss" scoped>
.wrap {
  display: flex;
  height: 100vh;
  .main-panel {
    flex: 600;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #EAEAEA;
  }
  .nav {
    position: relative;
    flex: 80px;
    background-color: #333333;
    border-right: 2px solid #414141;
    button.items {
      width: 80px;
      height: 80px;
      border: none;
      outline: none;
      background-color: transparent;
      img {
        width: 100%;
      }
    }
    .toggle-panel {
      display: flex;
      justify-content: space-around;
      align-items: center;
      position: absolute;
      bottom: 30px;
      right: 15px;
      width: 90px;
      height: 50px;
      background-color: #FCFCFC;
      border-radius: 25px 5px 5px 25px;
      img {
        width: 20px;
      }
      .arrow {
        transform: rotate(180deg);
      }
      &.open .arrow {
        transform: rotate(0);
      }
    }
  }
  .func-panel {
    flex: 500;
    background-color: #333333;
  }
}
</style>
